import {
	ArrowForwardIcon,
	CheckCircleIcon,
	Search2Icon,
} from '@chakra-ui/icons'
import {
	Box,
	Button,
	Text,
	Container,
	Input,
	Radio,
	RadioGroup,
	Spinner,
	Stack,
	InputGroup,
	InputLeftAddon,
	useBoolean,
	Center,
	Modal,
	ModalBody,
	ModalCloseButton,
	ModalContent,
	ModalHeader,
	ModalOverlay,
	useDisclosure,
	List,
	ListItem,
	ListIcon,
	ModalFooter,
	Badge,
} from '@chakra-ui/react'
import React, { useEffect, useState } from 'react'
import {
	useApiContract,
	useChain,
	useMoralis,
	useWeb3ExecuteFunction,
} from 'react-moralis'
import abi from '../abi.json'
import ADDRESS from '../contract'

const domains = ['ava', 'web3', 'dapp', 'ipfs', 'defi', 'gamefi']

function Transfer(): React.ReactElement {
	const { isWeb3Enabled, enableWeb3, account } = useMoralis()
	const { switchNetwork, chainId } = useChain()

	const { isOpen, onOpen, onClose } = useDisclosure()

	const [hostname, setHostname] = useState('')
	const [tld, setTld] = useState('2')
	const [ipfs, setIpfs] = useState('')

	const [hostnameChanged, setHostnameChanged] = useBoolean()

	const [dataClose, setDataClose] = useBoolean(true)

	const {
		runContractFunction: getId,
		data: dataGetId,
		isLoading: isLoadingGetId,
		isFetching: isFetchingGetId,
	} = useApiContract({
		abi,
		address: ADDRESS,
		functionName: 'getId',
		chain: 'avalanche testnet',
		params: { _domain: `${hostname}.${domains[parseInt(tld, 10)]}` },
	})

	const {
		runContractFunction: getDomain,
		data: dataGetDomain,
		isLoading: isLoadingGetDomain,
		isFetching: isFetchingGetDomain,
	} = useApiContract({
		abi,
		address: ADDRESS,
		functionName: 'domains',
		chain: 'avalanche testnet',
		params: { id: dataGetId },
	})

	const handleChangeHostname = (event: React.ChangeEvent<HTMLInputElement>) => {
		setHostnameChanged.on()
		let v = event.target.value
		domains.forEach((d, i) => {
			if (v.indexOf(`.${d}`) + 1) {
				v = v.replace(`.${d}`, '')
				setTld(`${i}`)
			}
		})
		setHostname(v)
	}

	const handleChangeIpfs = (event: React.ChangeEvent<HTMLInputElement>) =>
		setIpfs(event.target.value)

	const { data, fetch, isFetching, isLoading } = useWeb3ExecuteFunction()

	useEffect(() => {
		searchDomain()
	}, [tld]) // eslint-disable-line react-hooks/exhaustive-deps

	useEffect(() => {
		if (dataGetId && dataGetId !== '0') {
			;(async function changedId() {
				await getDomain()
			})()
		}
	}, [dataGetId]) // eslint-disable-line react-hooks/exhaustive-deps

	async function transferDomain() {
		if (!isWeb3Enabled) {
			enableWeb3()
		}
		await switchNetwork('0xa869')
		await fetch({
			params: {
				abi,
				contractAddress: ADDRESS,
				functionName: 'transferDomain',
				msgValue: JSON.parse(JSON.stringify(dataGetDomain || '{}')).price,
				params: {
					_tld: tld,
					_hostname: hostname,
					_ipfs: ipfs,
				},
			},
		})
		setDataClose.on()
	}

	async function searchDomain() {
		setHostnameChanged.off()
		setIpfs('')
		if (hostname) {
			await getId()
		}
		setDataClose.off()
	}

	return (
		<Container>
			<Box>
				<Box mb={5} textAlign='center'>
					<Text
						bgGradient='radial(pink.400, red.800)'
						bgClip='text'
						fontSize='3xl'
						fontWeight='extrabold'
					>
						{hostname ? (
							`${hostname}.${domains[parseInt(tld, 10)]}`
						) : (
							<Text as='span' onClick={onOpen} cursor='pointer'>
								How It Works?
							</Text>
						)}
					</Text>
				</Box>
				<Stack direction={['row']}>
					<Input
						value={hostname}
						onChange={handleChangeHostname}
						placeholder='example'
						spellCheck='false'
						color='white'
						borderColor='red.400'
						_placeholder={{ color: 'pink.700' }}
						_focus={{ boxShadow: 'none' }}
						_hover={{ borderColor: 'red.300' }}
					/>
					{isLoadingGetId || isFetchingGetId ? (
						<Button
							disabled
							rightIcon={<Spinner color='red.100' />}
							onClick={() => {
								searchDomain()
							}}
							px={5}
							colorScheme='red'
						>
							Search
						</Button>
					) : (
						<Button
							rightIcon={<Search2Icon ml={2} />}
							onClick={() => {
								searchDomain()
							}}
							px={5}
							colorScheme='red'
						>
							Search
						</Button>
					)}
				</Stack>
				<Box>
					<RadioGroup my={5} defaultValue={tld} onChange={setTld} value={tld}>
						<Stack spacing='auto' direction='row'>
							{domains.map((d, i) => {
								return (
									<Radio colorScheme='red' value={`${i}`} key={d}>
										.{d}
									</Radio>
								)
							})}
						</Stack>
					</RadioGroup>
				</Box>
				{!hostnameChanged && hostname && (
					<>
						<Box>
							{isLoadingGetDomain || isFetchingGetDomain ? (
								<Button isFullWidth disabled colorScheme='red'>
									<Spinner color='red.100' />
								</Button>
							) : (
								dataGetId &&
								dataGetId !== '0' &&
								dataGetDomain &&
								JSON.parse(JSON.stringify(dataGetDomain)).transfer.toLowerCase() !==
									(account || '').toLowerCase() && (
									<Button
										as='a'
										href={`https://ipfs.io/ipfs/${
											JSON.parse(JSON.stringify(dataGetDomain)).ipfs
										}`}
										target='_blank'
										colorScheme='red'
										isFullWidth
									>
										{JSON.parse(JSON.stringify(dataGetDomain)).ipfs}
									</Button>
								)
							)}
						</Box>
						{isFetching || isLoading ? (
							<Center my={5}>
								<Spinner size='xl' color='red.500' />
							</Center>
						) : (
							<>
								{' '}
								{data && dataClose ? (
									<Center my={5}>Domain transaction registered</Center>
								) : (
									<>
										{' '}
										{dataGetId &&
											dataGetId !== '0' &&
											dataGetDomain &&
											JSON.parse(JSON.stringify(dataGetDomain)).transfer.toLowerCase() ===
												(account || '').toLowerCase() && (
												<>
													<Box>
														<InputGroup colorScheme='red'>
															<InputLeftAddon
																color='white'
																borderColor='red.400'
																backgroundColor='pink.700'
															>
																ipfs://
															</InputLeftAddon>
															<Input
																onChange={handleChangeIpfs}
																placeholder='QmXoypizjW3WknFiJnKLwHCnL72vedxjQkDDP1mXWo6uco'
																spellCheck='false'
																color='white'
																borderColor='red.400'
																_placeholder={{ color: 'pink.700' }}
																_focus={{ boxShadow: 'none' }}
																_hover={{ borderColor: 'red.300' }}
															/>
														</InputGroup>
													</Box>
													<Box>
														<Button
															rightIcon={<ArrowForwardIcon />}
															isFullWidth
															mt={5}
															onClick={() => {
																transferDomain()
															}}
															colorScheme='red'
															disabled={!ipfs}
														>
															{chainId === '0xa869' ? (
																<>
																	Get domain name{' '}
																	{(
																		parseInt(
																			JSON.parse(JSON.stringify(dataGetDomain)).price,
																			10
																		) /
																		10 ** 18
																	).toFixed(2)}{' '}
																	AVAX
																</>
															) : (
																'Switch Network'
															)}
														</Button>
													</Box>
												</>
											)}
									</>
								)}
							</>
						)}
					</>
				)}
			</Box>
			<Modal onClose={onClose} isOpen={isOpen} isCentered>
				<ModalOverlay />
				<ModalContent bgGradient='linear(to-r, pink.900, red.900)' color='red.100'>
					<ModalHeader>How It Works?</ModalHeader>
					<ModalCloseButton />
					<ModalBody>
						<List spacing={3}>
							<ListItem>
								<ListIcon as={CheckCircleIcon} color='green.500' />
								Domain names can be{' '}
								<Badge colorScheme='red' variant='outline'>
									transferred
								</Badge>{' '}
								and{' '}
								<Badge colorScheme='pink' variant='outline'>
									sold
								</Badge>{' '}
								from user to user.
							</ListItem>
							<ListItem>
								<ListIcon as={CheckCircleIcon} color='green.500' />
								In the{' '}
								<Badge colorScheme='red' variant='outline'>
									Transfer
								</Badge>{' '}
								field, specify the address of the new domain owner, and price you want
								to get for this domain name
							</ListItem>
							<ListItem>
								<ListIcon as={CheckCircleIcon} color='green.500' />
								The new owner pays the specified price and receives the domain name
							</ListItem>
						</List>
					</ModalBody>
					<ModalFooter> </ModalFooter>
				</ModalContent>
			</Modal>
		</Container>
	)
}

export default Transfer
