import { AddIcon, CheckCircleIcon, WarningIcon } from '@chakra-ui/icons'
import { Badge, Box, Button, Center, Divider, Text } from '@chakra-ui/react'
import React, { useEffect } from 'react'

import { useChain, useMoralis } from 'react-moralis'

function Header(): React.ReactElement {
	const { Moralis, isWeb3Enabled, enableWeb3, account, chainId } = useMoralis()
	const { switchNetwork } = useChain()

	useEffect(() => {
		if (!isWeb3Enabled && (localStorage.getItem('metamask') || '') !== 'close') {
			enableWeb3()
			localStorage.setItem('metamask', 'close')
		}
	}, []) // eslint-disable-line react-hooks/exhaustive-deps

	async function connectToAvalancheFuji() {
		const FUJIchainId = 43113
		const chainName = 'Avalanche FUJI C-Chain'
		const currencyName = 'AVAX'
		const currencySymbol = 'AVAX'
		const rpcUrl = 'https://api.avax-test.network/ext/bc/C/rpc'
		const blockExplorerUrl = 'https://testnet.snowtrace.io/'

		if (!isWeb3Enabled) {
			enableWeb3()
		}

		await Moralis.addNetwork(
			FUJIchainId,
			chainName,
			currencyName,
			currencySymbol,
			rpcUrl,
			blockExplorerUrl
		)

		await switchNetwork('0xa869')
	}

	return (
		<Box>
			<Center height='30px' py={10} borderRadius='lg'>
				<Box mx={5}>
					<Text
						bgGradient='radial(pink.600, red.800)'
						bgClip='text'
						fontSize='3xl'
						fontWeight='extrabold'
					>
						AVADoma
					</Text>
					<Text
						bgGradient='radial(pink.500, red.900)'
						bgClip='text'
						fontWeight='extrabold'
						fontSize='xs'
					>
						beta testnet
					</Text>
				</Box>
				<Divider orientation='vertical' width='100%' />
				<Box mx={5} fontSize={15}>
					{!isWeb3Enabled ? (
						<Button
							onClick={() => {
								enableWeb3()
							}}
							colorScheme='red'
							size='xs'
							isFullWidth
						>
							<WarningIcon mr={2} fontSize={16} />
							Connect
						</Button>
					) : (
						<>
							{' '}
							{chainId !== '0xa869' ? (
								<Button
									onClick={() => {
										return connectToAvalancheFuji()
									}}
									colorScheme='red'
									size='xs'
									isFullWidth
								>
									<AddIcon mr={1} /> AVAX
								</Button>
							) : (
								<Badge
									variant='outline'
									colorScheme='red'
									p={1}
									border='none'
									shadow='none'
								>
									<Text fontSize='sm'>
										{(account || '').slice(0, 3)}...{(account || '').slice(-3)}
										<CheckCircleIcon ml={2} my={1} fontSize='lg' />
									</Text>
								</Badge>
							)}
						</>
					)}
				</Box>
			</Center>
		</Box>
	)
}

export default Header
