import {
	ArrowForwardIcon,
	Search2Icon,
	CheckCircleIcon,
	ExternalLinkIcon,
} from '@chakra-ui/icons'
import {
	Box,
	Button,
	Text,
	Container,
	Input,
	Radio,
	RadioGroup,
	Slider,
	SliderFilledTrack,
	SliderMark,
	SliderThumb,
	SliderTrack,
	Spinner,
	Stack,
	Tooltip,
	Link,
	InputGroup,
	InputLeftAddon,
	useBoolean,
	Center,
	List,
	ListIcon,
	ListItem,
	Modal,
	ModalBody,
	ModalCloseButton,
	ModalContent,
	ModalHeader,
	ModalOverlay,
	HStack,
	ModalFooter,
	Image,
	useDisclosure,
} from '@chakra-ui/react'
import React, { useEffect, useState } from 'react'
import {
	useApiContract,
	useChain,
	useMoralis,
	useWeb3ExecuteFunction,
} from 'react-moralis'
import abi from '../abi.json'
import ADDRESS from '../contract'

const domains = ['ava', 'web3', 'dapp', 'ipfs', 'defi', 'gamefi']

function Register(): React.ReactElement {
	const { isWeb3Enabled, enableWeb3 } = useMoralis()
	const { switchNetwork, chainId } = useChain()

	const { isOpen, onOpen, onClose } = useDisclosure()

	const [hostname, setHostname] = useState('')
	const [tld, setTld] = useState('2')
	const [ipfs, setIpfs] = useState('')
	const [year, setYear] = useState(5)
	const [value, setValue] = useState('500000000000000000')

	const [hostnameChanged, setHostnameChanged] = useBoolean()

	const [dataClose, setDataClose] = useBoolean(true)

	const [showTooltip, setShowTooltip] = useState(false)

	const {
		runContractFunction: getId,
		data: dataGetId,
		isLoading: isLoadingGetId,
		isFetching: isFetchingGetId,
	} = useApiContract({
		abi,
		address: ADDRESS,
		functionName: 'getId',
		chain: 'avalanche testnet',
		params: { _domain: `${hostname}.${domains[parseInt(tld, 10)]}` },
	})

	const {
		runContractFunction: getDomain,
		data: dataGetDomain,
		isLoading: isLoadingGetDomain,
		isFetching: isFetchingGetDomain,
	} = useApiContract({
		abi,
		address: ADDRESS,
		functionName: 'domains',
		chain: 'avalanche testnet',
		params: { id: dataGetId },
	})

	const handleChangeHostname = (event: React.ChangeEvent<HTMLInputElement>) => {
		setHostnameChanged.on()
		let v = event.target.value
		domains.forEach((d, i) => {
			if (v.indexOf(`.${d}`) + 1) {
				v = v.replace(`.${d}`, '')
				setTld(`${i}`)
			}
		})
		setHostname(v)
	}

	const handleChangeIpfs = (event: React.ChangeEvent<HTMLInputElement>) =>
		setIpfs(event.target.value)

	const { data, fetch, isFetching, isLoading } = useWeb3ExecuteFunction()

	useEffect(() => {
		if ((localStorage.getItem('help') || '') !== 'close') {
			onOpen()
		}
	}, []) // eslint-disable-line react-hooks/exhaustive-deps

	useEffect(() => {
		if (!hostname || !hostname.length) return
		const words = hostname.length
		if (words <= 1) {
			setValue((year * 10 * 10 ** 18).toString())
		} else if (words <= 3) {
			setValue((year * 10 ** 18).toString())
		} else if (words <= 5) {
			setValue((year * 0.1 * 10 ** 18).toString())
		} else if (words <= 63) {
			setValue((year * 0.01 * 10 ** 18).toString())
		}
	}, [hostname, year])

	useEffect(() => {
		searchDomain()
	}, [tld]) // eslint-disable-line react-hooks/exhaustive-deps

	useEffect(() => {
		if (dataGetId && dataGetId !== '0') {
			;(async function changedId() {
				await getDomain()
			})()
		}
	}, [dataGetId]) // eslint-disable-line react-hooks/exhaustive-deps

	async function registerDomain() {
		if (!isWeb3Enabled) {
			enableWeb3()
		}
		await switchNetwork('0xa869')
		await fetch({
			params: {
				abi,
				contractAddress: ADDRESS,
				functionName: 'registerDomain',
				msgValue: value,
				params: {
					_tld: tld,
					_hostname: hostname,
					_ipfs: ipfs,
				},
			},
		})
		setDataClose.on()
	}

	async function searchDomain() {
		setHostnameChanged.off()
		setIpfs('')
		if (hostname) {
			await getId()
		}
		setDataClose.off()
	}

	return (
		<Container>
			<Box>
				<Box mb={5} textAlign='center'>
					<Text
						bgGradient='radial(pink.400, red.800)'
						bgClip='text'
						fontSize='3xl'
						fontWeight='extrabold'
					>
						{hostname ? (
							`${hostname}.${domains[parseInt(tld, 10)]}`
						) : (
							<Text as='span' onClick={onOpen} cursor='pointer'>
								How It Works?
							</Text>
						)}
					</Text>
				</Box>
				<Stack direction={['row']}>
					<Input
						value={hostname}
						onChange={handleChangeHostname}
						placeholder='example'
						spellCheck='false'
						color='white'
						borderColor='red.400'
						_placeholder={{ color: 'pink.700' }}
						_focus={{ boxShadow: 'none' }}
						_hover={{ borderColor: 'red.300' }}
					/>
					{isLoadingGetId || isFetchingGetId ? (
						<Button
							disabled
							rightIcon={<Spinner color='red.100' />}
							onClick={() => {
								searchDomain()
							}}
							px={5}
							colorScheme='red'
						>
							Search
						</Button>
					) : (
						<Button
							rightIcon={<Search2Icon ml={2} />}
							onClick={() => {
								searchDomain()
							}}
							px={5}
							colorScheme='red'
						>
							Search
						</Button>
					)}
				</Stack>
				<Box>
					<RadioGroup my={5} defaultValue={tld} onChange={setTld} value={tld}>
						<Stack spacing='auto' direction='row'>
							{domains.map((d, i) => {
								return (
									<Radio colorScheme='red' value={`${i}`} key={d}>
										.{d}
									</Radio>
								)
							})}
						</Stack>
					</RadioGroup>
				</Box>
				{!hostnameChanged && hostname && (
					<>
						<Box>
							{isLoadingGetDomain || isFetchingGetDomain ? (
								<Button isFullWidth disabled colorScheme='red'>
									<Spinner color='red.100' />
								</Button>
							) : (
								dataGetId &&
								dataGetId !== '0' &&
								dataGetDomain && (
									<Button
										as='a'
										href={`https://ipfs.io/ipfs/${
											JSON.parse(JSON.stringify(dataGetDomain)).ipfs
										}`}
										target='_blank'
										colorScheme='red'
										isFullWidth
									>
										{JSON.parse(JSON.stringify(dataGetDomain)).ipfs}
									</Button>
								)
							)}
						</Box>
						{isFetching || isLoading ? (
							<Center my={5}>
								<Spinner size='xl' color='red.500' />
							</Center>
						) : (
							<>
								{' '}
								{data && dataClose ? (
									<Center my={5}>Domain transaction registered</Center>
								) : (
									<>
										{' '}
										{dataGetId && dataGetId === '0' && (
											<>
												<Box>
													<InputGroup colorScheme='red'>
														<InputLeftAddon
															color='white'
															borderColor='red.400'
															backgroundColor='pink.700'
														>
															ipfs://
														</InputLeftAddon>
														<Input
															value={ipfs}
															onChange={handleChangeIpfs}
															placeholder='QmXoypizjW3WknFiJnKLwHCnL72vedxjQkDDP1mXWo6uco'
															spellCheck='false'
															color='white'
															borderColor='red.400'
															_placeholder={{ color: 'pink.700' }}
															_focus={{ boxShadow: 'none' }}
															_hover={{ borderColor: 'red.300' }}
														/>
													</InputGroup>
												</Box>
												<Box>
													<Slider
														id='slider'
														my={5}
														defaultValue={year}
														min={1}
														max={10}
														colorScheme='pink'
														onChange={setYear}
														onMouseEnter={() => setShowTooltip(true)}
														onMouseLeave={() => setShowTooltip(false)}
													>
														<SliderMark value={1} mt='1' ml='-2.5' fontSize='sm'>
															1
														</SliderMark>
														<SliderMark value={2} mt='1' ml='-2.5' fontSize='sm'>
															2
														</SliderMark>
														<SliderMark value={3} mt='1' ml='-2.5' fontSize='sm'>
															3
														</SliderMark>
														<SliderMark value={4} mt='1' ml='-2.5' fontSize='sm'>
															4
														</SliderMark>
														<SliderMark value={5} mt='1' ml='-2.5' fontSize='sm'>
															5 years
														</SliderMark>
														<SliderMark value={6} mt='1' ml='-2.5' fontSize='sm'>
															6
														</SliderMark>
														<SliderMark value={7} mt='1' ml='-2.5' fontSize='sm'>
															7
														</SliderMark>
														<SliderMark value={8} mt='1' ml='-2.5' fontSize='sm'>
															8
														</SliderMark>
														<SliderMark value={9} mt='1' ml='-2.5' fontSize='sm'>
															9
														</SliderMark>
														<SliderMark value={10} mt='1' ml='-2.5' fontSize='sm'>
															10
														</SliderMark>
														<SliderTrack>
															<SliderFilledTrack />
														</SliderTrack>
														<Tooltip
															hasArrow
															bg='red.500'
															color='white'
															placement='top'
															isOpen={showTooltip}
															label={`${(parseInt(value, 10) / 10 ** 18).toFixed(2)} AVAX`}
														>
															<SliderThumb />
														</Tooltip>
													</Slider>
												</Box>
												<Box>
													<Button
														rightIcon={<ArrowForwardIcon />}
														isFullWidth
														mt={5}
														onClick={() => {
															registerDomain()
														}}
														colorScheme='red'
														disabled={!ipfs}
													>
														{chainId === '0xa869' ? (
															<>Register {(parseInt(value, 10) / 10 ** 18).toFixed(2)} AVAX</>
														) : (
															'Switch Network'
														)}
													</Button>
												</Box>
											</>
										)}
									</>
								)}
							</>
						)}
					</>
				)}
			</Box>
			<Modal
				onClose={() => {
					localStorage.setItem('help', 'close')
					onClose()
				}}
				isOpen={isOpen}
				isCentered
			>
				<ModalOverlay />
				<ModalContent bgGradient='linear(to-r, pink.900, red.900)' color='red.100'>
					<ModalHeader>How It Works?</ModalHeader>
					<ModalCloseButton />
					<ModalBody>
						<List spacing={3}>
							<ListItem>
								<ListIcon as={CheckCircleIcon} color='green.500' />
								Add the browser extension
								<HStack mt={5}>
									<Button
										as='a'
										colorScheme='red'
										isFullWidth
										href='https://avadoma.com/extensions.zip'
									>
										<Image
											borderRadius='full'
											boxSize='20px'
											src='chrome.png'
											alt='Chrome'
											mr={1}
										/>
										Chrome
									</Button>
									<Button
										disabled
										isFullWidth
										color='white'
										backgroundColor='yellow.700'
										_hover={{
											color: 'white',
											backgroundColor: 'yellow.700',
										}}
									>
										<Image
											borderRadius='full'
											boxSize='20px'
											src='firefox.png'
											alt='Firefox'
											mr={1}
										/>
										Firefox
									</Button>
									<Button disabled colorScheme='blue' isFullWidth>
										<Image
											borderRadius='full'
											boxSize='20px'
											src='safari.png'
											alt='Safari'
											mr={1}
										/>
										Safari
									</Button>
								</HStack>
							</ListItem>
							<ListItem>
								<ListIcon as={CheckCircleIcon} color='green.500' />
								Get some free AVAX (fuji) -{' '}
								<Link href='https://faucet.avax-test.network/' target='_blank'>
									faucet <ExternalLinkIcon mx='2px' />
								</Link>
							</ListItem>
							<ListItem>
								<ListIcon as={CheckCircleIcon} color='green.500' />
								Register your new domain name
							</ListItem>
						</List>
					</ModalBody>
					<ModalFooter> </ModalFooter>
				</ModalContent>
			</Modal>
		</Container>
	)
}

export default Register
