import { RepeatIcon } from '@chakra-ui/icons'
import {
	Badge,
	Box,
	Button,
	Center,
	IconButton,
	Spinner,
	Text,
} from '@chakra-ui/react'
import React, { useEffect, useState } from 'react'
import {
	useApiContract,
	useChain,
	useMoralis,
	useWeb3ExecuteFunction,
} from 'react-moralis'
import abi from '../abi.json'
import ADDRESS from '../contract'

function Lottery(): React.ReactElement {
	const { isWeb3Enabled, enableWeb3 } = useMoralis()
	const { switchNetwork } = useChain()

	const [randomDomain, setRandomDomain] = useState('xx.xxx')
	const examples = ['ex', 'hi', 'xx', 'qq', 'av', 'go']
	const TLDs = ['dapp', 'web3', 'gamefi', 'defi', 'ava', 'ipfs']

	const [hours, setHours] = useState('')
	const [minutes, setMinutes] = useState('')
	const [seconds, setSeconds] = useState('')

	const { runContractFunction: getLotteryCount, data: dataGetLotteryCount } =
		useApiContract({
			abi,
			address: ADDRESS,
			functionName: 'getLotteryCount',
			chain: 'avalanche testnet',
		})

	const { data, error, fetch, isFetching, isLoading } = useWeb3ExecuteFunction()

	function intervals() {
		setInterval(() => {
			setRandomDomain(
				`${examples[Math.floor(Math.random() * examples.length)]}.${
					TLDs[Math.floor(Math.random() * TLDs.length)]
				}`
			)
		}, 1000)

		setInterval(() => {
			const date = new Date()
			setSeconds(zero(23 - date.getHours()))
			setMinutes(zero(59 - date.getMinutes()))
			setHours(zero(59 - date.getSeconds()))
		}, 1000)

		function zero(num: number) {
			if (parseInt(`${num}`, 10) >= 0 && parseInt(`${num}`, 10) < 10) {
				return `0${num}`
			}
			return `${num}`
		}
	}

	async function regInLottery() {
		if (!isWeb3Enabled) {
			enableWeb3()
		}
		await switchNetwork('0xa869')
		await fetch({
			params: {
				abi,
				contractAddress: ADDRESS,
				functionName: 'regInLottery',
				params: {},
			},
		})
	}

	useEffect(() => {
		intervals()
	}, []) // eslint-disable-line react-hooks/exhaustive-deps

	useEffect(() => {
		glc()
	}, [data, error]) // eslint-disable-line react-hooks/exhaustive-deps

	async function glc() {
		await getLotteryCount()
	}

	return (
		<Box>
			<Center
				fontSize='xxx-large'
				fontFamily='monospace'
				fontWeight='extrabold'
				mb={5}
			>
				<Box
					as='span'
					padding={10}
					backgroundColor='red.900'
					borderRadius={10}
					py={5}
					px={7}
				>
					{seconds}
				</Box>
				:
				<Box
					as='span'
					padding={10}
					backgroundColor='red.900'
					borderRadius={10}
					py={5}
					px={7}
				>
					{minutes}
				</Box>
				:
				<Box
					as='span'
					padding={10}
					backgroundColor='red.900'
					borderRadius={10}
					py={5}
					px={7}
				>
					{hours}
				</Box>
			</Center>
			<Center my={5} fontSize='x-large' fontWeight='extrabold' color='red.900'>
				{!dataGetLotteryCount || (
					<IconButton
						aria-label='Update'
						icon={<RepeatIcon />}
						onClick={() => {
							glc()
						}}
						size='xs'
						colorScheme='pink'
						backgroundColor='red.800'
						color='red.500'
						mr={2}
					/>
				)}{' '}
				{(dataGetLotteryCount && (
					<Box
						as='span'
						padding={5}
						backgroundColor='red.900'
						borderRadius={5}
						py={1}
						px={3}
						mx={2}
						color='red.500'
					>
						{dataGetLotteryCount}
					</Box>
				)) || (
					<IconButton
						aria-label='Update'
						icon={<RepeatIcon />}
						onClick={() => {
							glc()
						}}
						size='xs'
						colorScheme='pink'
						backgroundColor='red.800'
						color='red.500'
						mr={2}
					/>
				)}{' '}
				participant(s){' '}
			</Center>
			<Box backgroundColor='pink.900' borderRadius='xl' p={10}>
				<Text
					mb={5}
					textAlign='center'
					bgGradient='linear(to-r, pink.400, red.400)'
					bgClip='text'
				>
					Every day a random domain name <br />
					<Badge colorScheme='red' variant='outline'>
						FREE
					</Badge>{' '}
					<Badge colorScheme='pink' variant='outline'>
						{randomDomain}
					</Badge>
					<br />
					is drawn in the lottery.
				</Text>
				{isFetching || isLoading ? (
					<Button
						colorScheme='red'
						size='lg'
						fontSize='lg'
						isFullWidth
						py={10}
						disabled
					>
						<Spinner color='red.100' />
					</Button>
				) : (
					<>
						{' '}
						{error ? (
							<Button
								colorScheme='red'
								size='lg'
								fontSize='lg'
								isFullWidth
								py={10}
								disabled
							>
								ERROR TRANSACTION
							</Button>
						) : (
							<>
								{' '}
								{data ? (
									<Button
										colorScheme='red'
										size='lg'
										fontSize='lg'
										isFullWidth
										py={10}
										disabled
									>
										TRANSACTION SENDED
									</Button>
								) : (
									<Button
										colorScheme='red'
										size='lg'
										fontSize='lg'
										isFullWidth
										py={10}
										onClick={() => {
											regInLottery()
										}}
									>
										FREE PARTICIPATION
										<Box fontSize='xs' ml={2} color='red.300'>
											(only gas)
										</Box>
									</Button>
								)}
							</>
						)}
					</>
				)}
			</Box>
		</Box>
	)
}

export default Lottery
