import {
	ArrowBackIcon,
	ArrowForwardIcon,
	ArrowRightIcon,
} from '@chakra-ui/icons'
import {
	Box,
	Button,
	Container,
	Text,
	Stack,
	Link,
	Center,
	Accordion,
	AccordionItem,
	AccordionButton,
	AccordionPanel,
	AccordionIcon,
	Input,
	InputGroup,
	InputLeftAddon,
	Slider,
	SliderMark,
	SliderFilledTrack,
	SliderTrack,
	Tooltip,
	SliderThumb,
	Spinner,
} from '@chakra-ui/react'
import React, { useEffect, useState } from 'react'
import {
	useApiContract,
	useChain,
	useMoralis,
	useWeb3ExecuteFunction,
} from 'react-moralis'
import abi from '../abi.json'
import ADDRESS from '../contract'

const TLDs = ['ava', 'web3', 'dapp', 'ipfs', 'defi', 'gamefi']

function DateFormat({ timestamp, plus }: { timestamp: string; plus: number }) {
	let d = new Date(parseInt(timestamp, 10) * 1000)
	if (plus) {
		const year = d.getFullYear()
		const month = d.getMonth()
		const day = d.getDate()
		d = new Date(year, month, day + 365 * plus)
	}
	return `${d.getDate() < 10 ? `0${d.getDate()}` : d.getDate()}/${
		d.getMonth() + 1 < 10 ? `0${d.getMonth() + 1}` : d.getMonth() + 1
	}/${d.getFullYear()}`
}

function DomainItem({
	domain: [tld, hostname, ipfsItem, , transferItem, priceItem, , deletedAtItem],
}: {
	domain: [string, string, string, string, string, string, string, string]
}) {
	const { isWeb3Enabled, enableWeb3 } = useMoralis()
	const { switchNetwork } = useChain()

	const hostnameTld = `${hostname}.${TLDs[parseInt(tld, 10)]}`

	const [ipfs, setIpfs] = useState(ipfsItem)
	const [transfer, setTransfer] = useState(transferItem)
	const [price, setPrice] = useState(priceItem)
	const [year, setYear] = useState(0)
	const [value, setValue] = useState('0')

	const [showTooltip, setShowTooltip] = useState(false)

	const { data, fetch, isFetching, isLoading, error } = useWeb3ExecuteFunction()

	useEffect(() => {
		if (!hostname || !hostname.length) return
		const words = hostname.length
		if (words <= 1) {
			setValue((year * 10 * 10 ** 18).toString())
		} else if (words <= 3) {
			setValue((year * 10 ** 18).toString())
		} else if (words <= 5) {
			setValue((year * 0.1 * 10 ** 18).toString())
		} else if (words <= 63) {
			setValue((year * 0.01 * 10 ** 18).toString())
		}
	}, [year]) // eslint-disable-line react-hooks/exhaustive-deps

	async function updateDomain() {
		console.log(value, tld, hostname, ipfs, transfer, price)
		if (!isWeb3Enabled) {
			enableWeb3()
		}
		await switchNetwork('0xa869')
		await fetch({
			params: {
				abi,
				contractAddress: ADDRESS,
				functionName: 'updateDomain',
				msgValue: value,
				params: {
					_tld: tld,
					_hostname: hostname,
					_ipfs: ipfs,
					_transfer: transfer || '0x000000000000000000000000000000000000dEaD',
					_price: `${price && price === '0' ? '1' : price}`,
				},
			},
		})
	}

	return (
		<AccordionItem border={0} my={3} key={hostnameTld}>
			<h2>
				<AccordionButton
					bgGradient='linear(to-r, red.700, pink.800)'
					borderRadius='md'
					_hover={{ bgGradient: 'linear(to-r, red.600, pink.700)' }}
				>
					<Box flex='1' textAlign='left' isTruncated>
						<ArrowForwardIcon mr={5} fontSize='2xl' />
						{hostnameTld}
					</Box>
					<AccordionIcon />
				</AccordionButton>
			</h2>
			<AccordionPanel pb={4}>
				<Center
					py={5}
					bgGradient='linear(to-r, pink.800, pink.700)'
					borderRadius='md'
					mb={5}
				>
					<Text fontWeight='extrabold'>
						<Link color='white' href={`http://${hostnameTld}`} target='_blank'>
							{hostnameTld}
						</Link>
					</Text>
					<ArrowRightIcon mx={5} />
					<Text fontWeight='extrabold'>
						<Link
							color='white'
							href={`https://dweb.link/ipfs/${ipfs}`}
							target='_blank'
						>
							ipfs://{(ipfs || []).slice(0, 3)}...
							{(ipfs || []).slice(-3)}
						</Link>
					</Text>
				</Center>
				<Box my={5}>
					<InputGroup colorScheme='red'>
						<InputLeftAddon
							color='white'
							borderColor='red.400'
							backgroundColor='pink.700'
						>
							ipfs://
						</InputLeftAddon>
						<Input
							value={ipfs}
							onChange={event => {
								setIpfs(event.target.value)
							}}
							placeholder='QmXoypizjW3WknFiJnKLwHCnL72vedxjQkDDP1mXWo6uco'
							spellCheck='false'
							color='white'
							borderColor='red.400'
							_placeholder={{ color: 'pink.700' }}
							_focus={{ boxShadow: 'none' }}
							_hover={{ borderColor: 'red.300' }}
						/>
					</InputGroup>
				</Box>
				<Box my={5}>
					<InputGroup colorScheme='red'>
						<InputLeftAddon
							color='white'
							borderColor='red.400'
							backgroundColor='pink.700'
						>
							Transfer for
						</InputLeftAddon>
						<Input
							value={transfer}
							onChange={event => {
								setTransfer(event.target.value)
							}}
							placeholder='0x0000000000000000000000000000000000000000'
							spellCheck='false'
							color='white'
							borderColor='red.400'
							_placeholder={{ color: 'pink.700' }}
							_focus={{ boxShadow: 'none' }}
							_hover={{ borderColor: 'red.300' }}
						/>
					</InputGroup>
				</Box>
				<Box my={5}>
					<InputGroup colorScheme='red'>
						<InputLeftAddon
							color='white'
							borderColor='red.400'
							backgroundColor='pink.700'
						>
							Price AVAX
						</InputLeftAddon>
						<Input
							defaultValue={(parseInt(price, 10) / 10 ** 18).toFixed(2)}
							onChange={event => {
								setPrice((parseFloat(event.target.value || '0') * 10 ** 18).toString())
							}}
							placeholder='0.01'
							spellCheck='false'
							color='white'
							borderColor='red.400'
							_placeholder={{ color: 'pink.700' }}
							_focus={{ boxShadow: 'none' }}
							_hover={{ borderColor: 'red.300' }}
						/>
					</InputGroup>
				</Box>
				<Box>
					<Slider
						id='slider'
						my={5}
						defaultValue={0}
						min={0}
						max={9}
						colorScheme='pink'
						onChange={setYear}
						onMouseEnter={() => setShowTooltip(true)}
						onMouseLeave={() => setShowTooltip(false)}
					>
						<SliderMark value={0} mt='1' ml='-2.5' fontSize='xs'>
							--
						</SliderMark>
						<SliderMark value={1} mt='1' ml='-2.5' fontSize='xs'>
							+1
						</SliderMark>
						<SliderMark value={2} mt='1' ml='-2.5' fontSize='xs'>
							+2
						</SliderMark>
						<SliderMark value={3} mt='1' ml='-2.5' fontSize='xs'>
							+3
						</SliderMark>
						<SliderMark value={4} mt='1' ml='-2.5' fontSize='xs'>
							+4
						</SliderMark>
						<SliderMark value={5} mt='1' ml='-2.5' fontSize='xs'>
							+5
						</SliderMark>
						<SliderMark value={6} mt='1' ml='-2.5' fontSize='xs'>
							+6
						</SliderMark>
						<SliderMark value={7} mt='1' ml='-2.5' fontSize='xs'>
							+7
						</SliderMark>
						<SliderMark value={8} mt='1' ml='-2.5' fontSize='xs'>
							+8
						</SliderMark>
						<SliderMark value={9} mt='1' ml='-2.5' fontSize='xs'>
							+9
						</SliderMark>
						<SliderTrack>
							<SliderFilledTrack />
						</SliderTrack>
						<Tooltip
							hasArrow
							bg='red.500'
							color='white'
							placement='top'
							isOpen={showTooltip}
							label={`${DateFormat({ timestamp: deletedAtItem, plus: year })}`}
						>
							<SliderThumb />
						</Tooltip>
					</Slider>
				</Box>
				{isFetching || isLoading ? (
					<Button colorScheme='red' isFullWidth my={5} disabled>
						<Spinner color='red.100' mr={2} />
					</Button>
				) : (
					<>
						{' '}
						{error || data ? (
							<Button colorScheme='red' isFullWidth my={5} disabled>
								{error ? 'ERROR TRANSACTION' : 'TRANSACTION SENDED'}
							</Button>
						) : (
							<Button
								onClick={() => {
									updateDomain()
								}}
								colorScheme='red'
								isFullWidth
								my={5}
							>
								UPDATE{' '}
								{value && value !== '0'
									? `${(parseInt(value, 10) / 10 ** 18).toFixed(2)} AVAX`
									: ''}
								<ArrowForwardIcon ml={2} />
							</Button>
						)}
					</>
				)}
			</AccordionPanel>
		</AccordionItem>
	)
}

function Update(): React.ReactElement {
	const { account } = useMoralis()

	const [owner, setOwner] = useState('')
	const [page, setPage] = useState(1)

	const {
		runContractFunction: getDomains,
		data: dataGetDomains,
		// isLoading: isLoadingGetDomains,
		// isFetching: isFetchingGetDomains,
	} = useApiContract({
		abi,
		address: ADDRESS,
		functionName: 'getDomains',
		chain: 'avalanche testnet',
		params: { owner, page: `${page}`, limit: '5' },
	})

	useEffect(() => {
		if (account) {
			setOwner(account)
		}
	}, [account]) // eslint-disable-line react-hooks/exhaustive-deps

	useEffect(() => {
		if (owner) {
			getDomains()
		}
	}, [owner, page]) // eslint-disable-line react-hooks/exhaustive-deps

	return (
		<Container>
			<Box>
				{dataGetDomains &&
				typeof dataGetDomains === 'object' &&
				Array.isArray(dataGetDomains || []) &&
				(JSON.parse(JSON.stringify(dataGetDomains)) || []).length ? (
					<Accordion allowToggle mt={5}>
						{(dataGetDomains && typeof dataGetDomains === 'object'
							? dataGetDomains
							: []
						).map(fullDomain => {
							return (
								fullDomain &&
								fullDomain[1] !== '' && (
									<DomainItem domain={fullDomain} key={fullDomain[1] + fullDomain[0]} />
								)
							)
						})}
					</Accordion>
				) : (
					<Center my={5} bgGradient='linear(to-r, pink.400, red.400)' bgClip='text'>
						You don&apos;t have domain names
					</Center>
				)}
				<Stack direction={['row']} mb={10}>
					{page >= 2 ? (
						<Button
							onClick={() => {
								setPage(page - 1)
							}}
							colorScheme='red'
							isFullWidth
						>
							<ArrowBackIcon mr={2} />
							PREW PAGE
						</Button>
					) : (
						''
					)}
					{(dataGetDomains || '').length >= 5 ? (
						<Button
							onClick={() => {
								setPage(page + 1)
							}}
							colorScheme='red'
							isFullWidth
						>
							NEXT PAGE
							<ArrowForwardIcon ml={2} />
						</Button>
					) : (
						''
					)}
				</Stack>
			</Box>
		</Container>
	)
}

export default Update
